import React , { Fragment , useState , useEffect} from 'react';

import { PageLoader } from '../shared/components';
import { ProjectPage } from './Styles';
import  ProjectNavbarLeft from './NavbarLeft'
import  ProjectNavbarTop from './NavbarTop';
import ProjectSidebar from './Sidebar';

const Project = () => {
    
    const [stateIsLoading , setStateOpen] = useState(true);

    useEffect(() => {
        const timer = setTimeout(() => setStateOpen(false), 2000);
        return () => clearTimeout(timer);
      }, []);

    if(stateIsLoading){
        return <PageLoader/>
    }else
    {
        return (
            <ProjectPage>
                <ProjectNavbarLeft/>
                <ProjectNavbarTop/>
                <ProjectSidebar/>
            </ProjectPage>
        );   
    }
};

export default Project;
